import { MouseEvent, useEffect, useState } from 'react';
import { StyledBtn, StyledHeader, StyledLink, StyledNav, StyledWrap } from './style';
import { useAppDispatch, useAppSelector } from '../../../../redux/hooks';

import { Badge } from '@doar/components';
import Label from '../label';
import ModalCreateChannel from '../modal-create-channel';
import { PlusCircle } from 'react-feather';
import { toggleChannel } from '../../../../redux/slices/chat-ui';
import { toggleSidebar } from '../../../../redux/slices/ui';

const SidebarChannels = () => {
    const dispatch = useAppDispatch();
    const { chatType } = useAppSelector(state => state.chatUI);
    const [show, setShow] = useState(false);
    const handleModal = () => {
        setShow(prev => !prev);
    };
    const [activeId, setActiveId] = useState('');

    useEffect(() => {
        dispatch(toggleChannel({ channel: 'general' }));
        setActiveId('channel-1');
    }, []);
    const handleClick = (e: MouseEvent<HTMLAnchorElement>) => {
        e.preventDefault();
        const target = e.target as HTMLElement;
        const { channel } = target.dataset;
        setActiveId(target.id);
        dispatch(toggleSidebar({ isOpen: undefined }));
        if (!channel) return;
        dispatch(toggleChannel({ channel }));
    };

    return (
        <>
            <StyledWrap>
                <StyledHeader>
                    <Label>All Channels</Label>
                    <StyledBtn onClick={handleModal}>
                        <PlusCircle size={14} />
                    </StyledBtn>
                </StyledHeader>
                <StyledNav>
                    <StyledLink
                        id="channel-1"
                        path="#!"
                        $active={activeId === 'channel-1' && chatType === 'channel'}
                        onClick={handleClick}
                        data-channel="general"
                    >
                        # general
                    </StyledLink>
                    <StyledLink
                        id="channel-2"
                        path="#!"
                        $active={activeId === 'channel-2' && chatType === 'channel'}
                        onClick={handleClick}
                        data-channel="engineering"
                    >
                        # engineering
                    </StyledLink>
                    <StyledLink
                        id="channel-3"
                        path="#!"
                        $active={activeId === 'channel-3' && chatType === 'channel'}
                        onClick={handleClick}
                        data-channel="products"
                    >
                        # products
                        <Badge color="danger" shape="circle" ml="auto">
                            2
                        </Badge>
                    </StyledLink>
                </StyledNav>
            </StyledWrap>
        </>
    );
};

export default SidebarChannels;
